<template>
  <div>
    <mt-button type="primary" @click="select"></mt-button>
    <p :value="address">{{address}}</p>
    <yz-select v-if="show" :ref="show" @changeValue='isShow' :province='province' :city='city' :county='county'></yz-select>
  </div>
</template>

<script>
import select from '@/components/select'
export default {
  name: 'HelloWorld',
  data () {
    return {
		  codeValue: '',
		  validCode: true,
      show: false,
      address: '',
      province: '',
      city: '',
      county: ''
    }
  },
  components:{
    'yz-select': select
  },
  methods: {
    select(){
			this.show = true;
    },
    isShow(msg){
			console.log(msg);
			this.show = msg.show;
			this.address = msg.result;
			this.province = msg.province;
			this.city = msg.city;
			this.county = msg.county;
		},
		getCode(){
			var time=60;
			var that = this;
			if(this.validCode){
				this.validCode = false;
				this.codeValue =  time + '';
				var t = setInterval(getTime,1000)
			}
			function getTime(){
				time--;
				that.codeValue =  time + '';
				if (time == 0) {
						clearInterval(t);
						that.codeValue = '';
						that.validCode=true;
				}
			}
		}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



